$primary:rgb(0, 132, 255);

$bg-blue-mode: lightblue;
$grey-blue-mode: darkviolet;
$blue-text: yellow;

$bg-dark-mode: #222;
$grey-dark-mode: rgba(66, 66, 66, 0.712);
$dark-text: #fff;

$bg-custom-mode: silver;
$grey-custom-mode: aqua;
$custom-text: green;

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.7;
  font-size: 1rem;
}

body.blue-mode {
    background-color: $bg-blue-mode;
    color: $blue-text;
    .card {
      background-color: $grey-blue-mode;
      box-shadow: none;
      padding: 10px;
      border-radius: 15px;
    }
  }

body.dark-mode {
  background-color: $bg-dark-mode;
  color: $dark-text;
  .card {
    background-color: $grey-dark-mode;
    box-shadow: none;
    padding: 10px;
    border-radius: 15px;
  }
}

body.custom-mode {
    background-color: $bg-custom-mode;
    color: $custom-text;
    .card {
      background-color: $grey-custom-mode;
      box-shadow: none;
      padding: 10px;
      border-radius: 15px;
    }
  }

.app {
  text-align: center;
  margin: 1rem auto;
  h1 {
    font-weight: 800;
    font-size: 2rem;
    color:$primary;
  }
  p {
    margin: 2rem 4rem ;
  }
}

section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  text-align: center;
  .card {
    margin: 4rem .5rem;
    box-shadow: 2px 1px 3px 2px rgb(240, 240, 240);
    p{
      word-break: break-all;
    }
  }
}

.custom-select{
  display: inline-block;
  width: 100%;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 1.75rem 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  vertical-align: middle;
  background:#fff url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='4' height='5'%3E%3Cpath fill='%23343a40' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E") right .75rem center/8px 10px no-repeat;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  -webkit-appearance: none;
  appearance: none;
  transition: background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}